import React, { useEffect, useState } from "react";
import { Product } from '../product';
import './index.css';

export default function ProductCarousel(props){

    const [currentIndex, setCurrentIndex] = useState(0);

    const [currentProduct, setCurrentProduct] = useState(props.products[0]);

    // display the current indexed product, 
    // this function will run when elements in the second parameter [currentIndex, props.products] changes.
    useEffect(() => {
        if(currentIndex <= 0) {
            document.getElementsByClassName('previousButton')[0].style.display = 'none';
        } else {
            document.getElementsByClassName('previousButton')[0].style.display = 'block';
        }

        if (currentIndex >= props.products.length - 1 ) {
            document.getElementsByClassName('nextButton')[0].style.display = 'none';
        } else {
            document.getElementsByClassName('nextButton')[0].style.display = 'block';
        }
        setCurrentProduct(props.products[currentIndex]);
    }, [currentIndex, props.products]);

    return (
        <div className="product-carousel">
            <button 
                className="previousButton" 
                onClick={() => currentIndex === 0 ? 0:setCurrentIndex(currentIndex-1)}>
                    Previous
            </button>
            <Product name={currentProduct.name} image={currentProduct.image}/>
            <button 
                className="nextButton" 
                onClick={() => currentIndex === props.products.length-1 ? props.products.length -1 : setCurrentIndex(currentIndex+1)}>
                    Next
            </button>
        </div>
    );

   
}